.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    opacity: 0;
    pointer-events: none;
};

.modal_active {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    
};

.modal_content {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    border-radius: 25px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.5s;
    width: 70vw;
    height: 54vw;

    @media screen and (min-width: 1024px) {
        width: 30vw;
        height: 40vh;
    }

    @media screen and (min-width: 1400px) {
        width: 20vw;
        height: 36vh;
    }
};

.modal_content_active {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    border-radius: 25px;
    background-color: white;
    transform: scale(1);
    transition: 0.5s;
    width: 70vw;
    height: 54vw;

    @media screen and (min-width: 1024px) {
        width: 30vw;
        height: 40vh;
    }

    @media screen and (min-width: 1400px) {
        width: 20vw;
        height: 36vh;
    }
};

.form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: "Manrope";
    font-style: normal;
    margin-top: auto;
    margin-bottom: auto;
};

.icon_close {
    width: 100%;
    height: 100%;
    color: rgba(0, 0, 0, 0.2);
};

.button_close {
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    width: 40px;
    height: 40px;
    border: none;
    padding: 0;
    cursor: pointer;
};

.button_submit {
    width: 220px;
    padding: 10px;
    background-color: orange;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-family: "Manrope";
    font-style: normal;
    font-weight: 700;
    margin-top: 20px;
    cursor: pointer;

    &:hover {
        background-color: darkorange;
    }
};

.input {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    margin-top: 8px;
    border: 2px solid rgba(0, 0, 0, 0.2);;
    border-radius: 10px;
    width: 200px;
    height: 30px;
    padding-left: 10px;
    padding-right: 10px;
    outline: none;

    &:focus {
        border: 2px solid orange;;
    }
};

.input_label {
    margin-top: 10px;
    font-family: "Manrope";
    font-style: normal;
    font-weight: 700;
};